* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
::-webkit-scrollbar {
  display: none;
}
html {
  background-color: whitesmoke;
  overflow-x: hidden;
}
a:visited {
  text-decoration: none;
  color: #000;
}
a:hover {
  text-decoration: none;
  color: #000;
}
a:focus {
  text-decoration: none;
  color: #000;
}
a:hover,
a:active {
  text-decoration: none;
  color: #000;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.estminswatched {
  width: 30%;
  height: 45vh;
  margin-top: 30vh;
  margin-left: 10vw;
  border-style: solid;
  border-width: 1px;
}
.video {
  transition: ease-in-out 0.2s;
}
.video:hover {
  transform: scale(1.025);
  transition: ease-in-out 0.2s;
}
.footer {
  bottom: 1vw;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0vw;
  right: 0vw;
  text-align: center;
  opacity: 50%;
  font-size: 0.75vw;
  font-weight: lighter;
}
/* Graph Style*/
.graphcontainer {
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin: 1vw;
  padding: 1vw;
  padding-left: 2vw;
  padding-right: 2vw;
  margin-left: 0;
  background-color: white;
  border-radius: 0.15vw;
  box-shadow: 2px 5px 8px 2px #00000020;
}

svg {
  width: 35vw;
  height: 25vw;
  background: white;
  overflow: visible;
  margin-bottom: 2rem;
  display: block;
}
.tick line {
  visibility: hidden;
}
button {
  background-color: #ff3d5d;
  border: none;
  color: white;
  border-radius: 0.25vw;
  margin: 0.5vw;
  width: 5vw;
  height: 2vw;
}
