.navbar {
    float: left;
    background-color: white;
    position: fixed;
    z-index: 4;
    top: 0;
    width: 3.5rem;
    overflow-x: hidden;
    overflow-y: hidden;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    transition: ease-in-out 0.3s;
  }
  .rect{
    position: fixed;
    left: 0;
    width: 5px;
    height: 4rem;
    background-color: #ff3d3d;
    opacity: 0.8;
    transition: all 0.2s;
    z-index: 5;
  }
  .menu-icon{
    border: none;
    background: none;
    z-index: 5;
    opacity: 0.6;
    padding: 0.75rem;
    padding-top: 1rem;
    width: 2rem;
    transition: ease-in-out 0.2s;
    cursor: pointer;
  }
  .menu-icon:hover{
    opacity: 0.6;
    transition: ease-in-out 0.2s;
  }
  .navbaricon {
    position: fixed;
    width: 2rem;
    padding: 0.75rem;
    float: left;
    opacity: .95;
    z-index: 5;
  }
  
  .navlink {
    overflow: hidden;
    margin-top: 1.5rem;
    width: 20rem;
    height: 5rem;
    font-size: 1.5rem;
    transition: ease-in-out 0.2s;
    opacity: .7;
    z-index: 5;
    display: flex;
    align-items: center;
  }
  .navlink:hover {
    opacity: 1;
    transition: ease-in-out 0.2s;
    cursor: pointer;
    color: red;
    transform: scale(1.01);
    transition: ease-in-out 0.2s;
  }
  .navlinktext {
    margin-left: 5rem;
  }
  hr {
    opacity: .1;
  }