
.profile-info-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 20rem; 
}
.profile-info{
    margin-left: 2rem;
    position: relative;
}
.subscriber-count, .view-count, .video-count{
    font-size: 1.5rem;
    color: rgb(111,111,111);
    font-Weight: lighter;
    opacity: 0.5;
}
.subscriber-count{
    color: red;
}
.user-name{
    font-size: 3rem;
}
.profile-pic{
    width: 12rem;
    border-radius: 50%;
}
.youtube-link-icon{
    display: inline;
    position: absolute;
    top: 0;
    width: 1rem;
    transition: ease-in-out 0.2s;
    opacity: 0.5;
}
.youtube-link-icon:hover{
    cursor: pointer;
    transform: scale(1.2);
    transition: ease-in-out 0.2s;
    opacity: 0.8;
}
h2{
    font-size: 2rem;
    margin: 2rem;
    opacity: 0.7
}
.top-channels-title{
    margin: 0;
    margin-left: 2vw;
}
.genres {
    box-shadow: 2px 5px 8px 2px #00000020;
    overflow: hidden;
    margin-top: 4em;
    margin-bottom: 4em;
    margin-left: 5vw;
    padding: 1vw;
    width: 90vw;
    height: 25vw;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25vw;
  }

  .topchannels{
    box-shadow: 2px 5px 8px 2px #00000020;
    overflow: hidden;
    margin-left: 5vw;
    padding: 1rem;
    width: 90vw;
    height: 42.5vw;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25vw;
  }
  .topchannels div {
    display: inline-block;
  }
  .genres div{
    display: inline-block;
  }

